<template>
  <div id="myCarousel" class="carousel slide">
    <ol class="carousel-indicators">
      <li data-target="#myCarousel" data-slide-to="0" class="active"></li>
      <li data-target="#myCarousel" data-slide-to="1"></li>
      <li data-target="#myCarousel" data-slide-to="2"></li>
    </ol>
    <div class="carousel-inner" v-if="imgs.length">
      <div class="item active" style="background:#DE2A2D">
        <img :src="imgs[0]" alt="">
      </div>
      <div class="item" style="background:#F5E4DC;">
        <img :src="imgs[1]" alt="">
      </div>
      <div class="item" style="background:#0000c6;">
        <img :src="imgs[2]" alt="">
      </div>
    </div>
    <a href="#myCarousel" data-slide="prev" class="carousel-control left">
      <span class="glyphicon glyphicon-chevron-left"></span>
    </a>
    <a href="#myCarousel" data-slide="next" class="carousel-control right">
      <span class="glyphicon glyphicon-chevron-right"></span>
    </a>
  </div>
</template>
<script type='text/ecmascript-6'>
  import $ from 'jquery'
  export default {
    props:{
      imgs: { //
        type: Array,
        default: ['http://placehold.it/960x440','http://placehold.it/960x440','http://placehold.it/960x440']
      }
    },
    data() {
      return {}
    },
    mounted(){
      this.slide()   //
    },
    methods: {
      // 
      slide () {
        $(function () {
          // 

          // 
          var $carousels = $('.carousel')
          var startX, endX
          // 
          var offset = $carousels.width() * 0.2
          // 
          $carousels.on('touchstart', function (e) {
            // x
            startX = e.originalEvent.touches[0].clientX
          })
          $carousels.on('touchmove', function (e) {
            //  move
            endX = e.originalEvent.touches[0].clientX
          })
          $carousels.on('touchend', function (e) {
            // console.log(endX);
            // x endX
            // endXstartX
            var distance = Math.abs(startX - endX)
            if (distance > offset) {
              // 
              //  
              $(this).carousel(startX > endX ? 'next' : 'prev')
            }
          })
        })
      },
    },
    computed: {},
    components: {}
  }
</script>
<style scoped>
  #myCarousel {
    margin: 50px 0 0 0;
  }

  #navbar-collapse ul {
    margin-top: 0;
  }

  .carousel-indicators {
    bottom: -5px;
  }

  .carousel-inner img {
    margin: 0 auto;
  }
</style>



